import { useState } from "react";

const Header = ({ logo }) => {

    const [links, setLinks] = useState(false)
    const handleLinks = (e) => {
        e.preventDefault();
        setLinks(!links);
    }


    return (
        <header>
            <img src={logo} alt="Shortly" className="mobile-logo" />
            <div className="menu-icon" onClick={handleLinks}>
                {links ? <ion-icon name="close-outline"></ion-icon> : <ion-icon name="menu-outline"></ion-icon>}
            </div>
            {links && <div className="nav-links">
                <ul className="nav-links-e">
                    <li><a href="#">Features</a></li>
                    <li><a href="#">Pricing</a></li>
                    <li><a href="#">Resources</a></li>
                </ul>
                <hr />
                <ul className="btns">
                    <button id="login"><li><a href="#">Login</a></li></button>
                    <button id="signup"><li><a href="#">Sign Up</a></li></button>
                </ul>
            </div>}
            <div className="desktop-header">
                <div className="nav-links-desktop">
                    <img src={logo} alt="Shortly" />
                    <ul>
                        <li><a href="#">Features</a></li>
                        <li><a href="#">Pricing</a></li>
                        <li><a href="#">Resources</a></li>
                    </ul>
                </div>
                <div>
                    <ul className="btns">
                        <button id="login"><li><a href="#">Login</a></li></button>
                        <button id="signup"><li><a href="#">Sign Up</a></li></button>
                    </ul>
                </div>
            </div>
        </header>
    );
}

export default Header;