import { useState } from "react";
import CopyToClipboard from "react-copy-to-clipboard";

const Shorten = () => {
    let [link, setLink] = useState('');
    const [showLinks, setShowLinks] = useState(false);
    const [originalLink, setOriginalLink] = useState('');
    const [shortLink, setShortLink] = useState('');
    const [showAdd, setShowAdd] = useState(false);
    const [shortenIt, setShortenIt] = useState("Shorten It!")
    const [copy, setCopy] = useState('Copy');


    const getShortLink = (e) => {
        e.preventDefault();
        setShowAdd(false);
        setShortenIt("Shortening...")
        document.querySelector('input').classList.remove('empty');

        fetch('https://api.shrtco.de/v2/shorten?url=' + link)
            .then(res => {
                if (!res.ok) {
                    throw Error('Unknown error occurred')
                } return res.json()
            })
            .then((data) => {
                const { result: { original_link, full_short_link } } = data;
                setOriginalLink(original_link);
                setShortLink(full_short_link);
                setShortenIt("Shorten It!")
                setShowLinks(true);
                setLink('');
                setCopy('Copy');
                document.querySelector('.copy').classList.remove('copied');
            })
            .catch(err => console.log(err.message))



        if (!link) {
            document.querySelector('input').classList.add('empty');
            setShowAdd(true);
            setShortenIt("Shorten It!")
        }
    }

    const onCopyText = () => {
        setCopy('Copied!');
        document.querySelector('.copy').classList.add('copied');
    }

    return (
        <div className="shorten">
            <div className="shorten-it">
                <input
                    type="text"
                    value={link}
                    onChange={(e) => setLink(e.target.value)}
                    placeholder="Shorten a link here..." />
                {showAdd && <small>Please add a link</small>}
                <button onClick={getShortLink}>{shortenIt}</button>
            </div>
            {showLinks && <div className="links-container">
                {showLinks && <div className="links">
                    <h3>{originalLink}</h3>
                    <h3 id="short-link">{shortLink}</h3>
                    <CopyToClipboard text={shortLink} onCopy={onCopyText}>
                        <button className="copy">{copy}</button>
                    </CopyToClipboard>
                </div>}
            </div>}
        </div>
    );
}

export default Shorten;